html {
  scroll-behavior: smooth;
}

.section {
  width: 100%;
  padding-top: 80px;
  padding-bottom: 80px;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 40px 20px;
}

body {
  background-color: #0B0D17;
}

h1 {
  font-family: "Bellefair", serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 150px;
  line-height: 172px;
  text-transform: uppercase;
  margin: 0;
}

h2 {
  font-family: "Bellefair", serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 100px;
  line-height: 115px;
  text-transform: uppercase;
  margin: 0;
}

h3 {
  font-family: "Bellefair", serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 56px;
  line-height: 64px;
  text-transform: uppercase;
  margin: 0;
}

h4 {
  font-family: "Bellefair", serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
}

h5 {
  font-family: "Bellefair", serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 4.725px;
}

.subheading__main {
  font-family: "Bellefair", serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 28px;
  line-height: 32px;
  text-transform: uppercase;
  margin: 0;
}

.subheading__2 {
  font-family: "Barlow Condensed", sans-serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 2.3625px;
  text-transform: uppercase;
  margin: 0 0 20px;
}

.nav-text {
  font-family: "Barlow", sans-serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  margin: 0;
}

p {
  font-family: "Barlow", sans-serif;
  color: #FFFFFF;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
}

a {
  text-decoration: none;
}

.width_description {
  max-width: 460px;
}

.thin_divider {
  width: 100%;
  height: 1px;
  background-color: #383B4B;
  margin: 50px 0 30px;
}

.subheading {
  font-family: "Barlow Condensed";
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  text-transform: uppercase;
  letter-spacing: 4.725px;
  color: #FFFFFF;
}

.section_number {
  font-family: "Barlow Condensed";
  font-weight: bold;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 4.725px;
  margin-right: 25px;
  color: #383B4B;
}

.page_section {
  height: 100vh;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #0B0D17;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

#Home {
  background-image: url(/assets/home/background-home-desktop.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

#Home .container {
  display: flex;
  align-items: end;
  height: 100%;
  padding-bottom: 150px;
}

#Home .left_container {
  width: 70%;
}

#Home .right_container {
  width: 30%;
}

#Home .left_container h1 {
  font-weight: 400;
  font-size: 150px;
  margin: 25px 0;
}

#explore_circle {
  text-align: center;
}

#Home #explore_circle span a {
  font-family: Bellefair, serif;
  color: #0B0D17;
  font-weight: 400;
  font-size: 32px;
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: #FFFFFF;
  border-radius: 50%;
  padding: 120px 64px;
  display: inline-block;
}

#Destination {
  background-image: url(/assets/destination/background-destination-desktop.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

#Destination .container {
  padding: 340px 20px 80px;
}

#Destination .destination_sub-container {
  max-width: 90%;
  margin: auto;
}

#Destination .tab {
  display: flex;
  margin: 80px 0 52px;
}

#Destination .tab .tab_left_container {
  width: 55%;
}

#Destination .tab .tab_container {
  width: 45%;
}

#Destination .tab span:first-child {
  margin: 0 !important;
}

#Destination .tab span {
  font-family: "Barlow Condensed";
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  line-height: 19px;
  letter-spacing: 2.7px;
  color: #FFFFFF;
  cursor: pointer;
  padding: 12px 0;
  margin-left: 35px;
}

#Destination .tab span:hover {
  border-bottom: 3px solid #8a8b90;
}

#Destination .tab span.active {
  border-bottom: 3px solid #FFFFFF;
}

#Destination .tabcontent {
  display: none;
}

#Destination .planet_container {
  display: flex;
}

#Destination .planet_image {
  width: 55%;
}

#Destination .planet_content {
  width: 45%;
}

#Destination .planet_image img {
  width: 100%;
  max-width: 500px;
  margin-top: -40px;
}

#Destination .planet_info {
  display: flex;
}

#Destination .plant_distance {
  margin-right: 80px;
}

#Crew {
  background-image: url(/assets/crew/background-crew-desktop.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

#Crew .container {
  padding: 340px 20px 0;
}

#Crew .tab {
  display: flex;
  margin-top: -60px;
}

#Crew .tab .tab_left_container {
  width: 55%;
}

#Crew .tab .tab_container {
  width: 45%;
}

#Crew .tab span:first-child {
  margin: 0 !important;
}

#Crew .tab span {
  width: 18px;
  height: auto;
  background-color: #363841;
  display: inline-block;
  border-radius: 50%;
  padding: 9px 0;
  margin-left: 15px;
}

#Crew .tab span.active {
  background-color: #FFFFFF;
}

#Crew .tab span:hover {
  background-color: #FFFFFF;
}

#Crew .tabcontent {
  display: none;
}

#Crew .crew_container {
  display: flex;
  align-items: center;
}

#Crew .crew_image {
  width: 45%;
  text-align: center;
}

#Crew .crew_content {
  width: 65%;
}

.crew_subheading {
  font-family: "Bellefair";
  font-weight: 400;
  font-size: 32px;
  color: #8f9195;
  line-height: 37px;
  text-transform: uppercase;
  display: block;
  margin-bottom: 15px;
}

.crew_content h2 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 56px;
  line-height: 64px;
  text-transform: uppercase;
}

#Crew .planet_info {
  display: flex;
}

#Crew .plant_distance {
  margin-right: 80px;
}